@font-face {
  font-family: 'TESLA';
  src: local('TESLA'),url('./fonts/TESLA.ttf') format('true');
  font-weight: normal;
}


*{
  margin: 0;
}

body {
  margin: 0;
  font-family: 'TESLA', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
