.header{
    display: flex;
    background-color: transparent;
    background: none;
    position: fixed;
    width: 100vw;
    top: 0;
    margin: 15px;
    margin-top: 15px;
    z-index: 100;
}


.header_logo{
    height: 24px;
    width: 100px;
    margin-top: 7px;
    cursor: pointer;
} 

.header_center{
    flex: 1;
    display: flex;
    justify-content: center;
}

.header_center>p{
    margin: 10px;
    text-transform: uppercase;
    cursor: pointer;
}


.header_right{
    display: flex;
    justify-content: flex-end;
    text-transform: uppercase;
    margin-right: 30px;
  
}

.header_right>p{
    margin: 10px;  
    cursor: pointer;
    font-style: museo;
}